<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <el-row>
                <el-col>
                    <el-button type="primary" @click="showAddCateDialog"></el-button>
                </el-col>
            </el-row>

            <!--  -->
            <tree-table class="treeTable" :data="cateList" :columns="columns" :selection-type="false"
                :expand-type="false" show-index index-text="" border :show-row-hover="false">

                <!--  -->
                <template slot="isok" slot-scope="scope">
                    <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color:lightgreen"></i>
                    <i class="el-icon-error" v-else style="color:red"></i>
                </template>

                <!--  -->
                <template slot="order" slot-scope="scope">
                    <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
                    <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1"></el-tag>
                    <el-tag type="warning" size="mini" v-else-if="scope.row.cat_level === 2"></el-tag>
                </template>

                <!--  -->
                <template slot="opt" slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
                    <el-button type="danger" icon="el-icon-delete-solid" size="mini"></el-button>
                </template>
            </tree-table>

            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="querInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="querInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>

        <!-- -->
        <el-dialog title="" :visible.sync="addCateDialogVisible" width="50%" @close="addCateDialogClosed">
            <!--  -->
            <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
                <el-form-item label="" prop="cat_name">
                    <el-input v-model="addCateForm.cat_name"></el-input>
                </el-form-item>

                <el-form-item label="">
                    <!-- options -->
                    <el-cascader v-model="selectedKeys" :options="parentCateList" :props="cascaderProps"
                        @change="parentCateChanged" clearable></el-cascader>
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="addCateDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addCate"> </el-button>
            </span>
        </el-dialog>


    </div>
</template>

<script>
export default {
    data() {
        return {
            // 
            querInfo: {
                type: 3,
                pagenum: 1,
                pagesize: 5
            },
            // 
            cateList: [],
            // 
            total: 0,
            //table
            columns: [{
                label: '',
                prop: 'cat_name',
            }, {
                label: '',
                // 
                type: 'template',
                //
                template: 'isok'
            }, {
                label: '',
                // 
                type: 'template',
                //
                template: 'order'
            }, {
                label: '',
                // 
                type: 'template',
                //
                template: 'opt',
            }],
            // 
            addCateDialogVisible: false,
            //
            addCateForm: {
                // 
                cat_name: '',
                // id
                cat_pid: 0,
                //  1
                cat_level: 0
            },

            // 
            addCateFormRules: {
                cat_name: [
                    {
                        required: true, message: '', trugger: 'blur'
                    }
                ]
            },
            // 
            parentCateList: [],
            // 
            cascaderProps: {
                expandTrigger: 'hover',
                value: 'cat_id',
                label: 'cat_name',
                childdren: 'children',
                checkStrictly: true
            },
            // id
            selectedKeys: []
        }
    },
    created() {
        this.getCateList()
    },
    methods: {
        //
        async getCateList() {
            const { data: res } = await this.$http.get('categories', { params: this.querInfo })

            if (res.meta.status !== 200) {
                return this.$message.error('')
            }

            //cateList
            this.cateList = res.data.result

            // 
            this.total = res.data.total

        },
        // pagesize
        handleSizeChange(newSize) {
            this.querInfo.pagesize = newSize
            this.getCateList()
        },
        //pagenum
        handleCurrentChange(newPage) {
            this.querInfo.pagenum = newPage
            this.getCateList()
        },
        //  
        showAddCateDialog() {
            //
            this.getParentCateList()
            // 
            this.addCateDialogVisible = true

        },
        // 
        async getParentCateList() {
            const { data: res } = await this.$http.get('categories', { params: { type: 2 } })

            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.parentCateList = res.data
        },
        parentCateChanged() {
            console.log(this.selectedKeys)
            // selectedKeyslentgth0 
            // 
            if (this.selectedKeys.length > 0) {
                // id
                this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
                // 
                this.addCateForm.cat_level = this.selectedKeys.length
                return
            } else {
                // id
                this.addCateForm.cat_pid = 0
                // 
                this.addCateForm.cat_level = 0
            }
        },
        // 
        addCate() {
            this.$refs.addCateFormRef.validate(async valid => {
                if (!valid) return
                const { data: res } = await this.$http.post('categories', this.addCateForm)

                if (res.meta.status !== 201) {
                    return this.$message.error('')

                }
                this.$message.success('')
                this.getCateList()
                this.addCateDialogVisible = false


            })

        },
        //  
        addCateDialogClosed() {
            this.$refs.addCateFormRef.resetFields()
            this.selectedKeys = []
            this.addCateForm.cat_level = 0
            this.addCateForm.pid = 0

        }

    },
}
</script>

<style lang="less" scoped>
.treeTable {
    margin-top: 15px;
}

.el-cascader {
    width: 100%;
}
</style>