<template>
  <div class="add">
    <Breadcrumb name1="" name2="" />
    <!--    -->
    <el-card>
      <!--      -->
      <el-alert title="" type="info" center :closable="false" show-icon> </el-alert>
      <!--      -->
      <el-steps align-center :active="activeIndex - 0" finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!--      table --- > tab-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-position="top" label-width="100px">
        <el-tabs :tab-position="'left'" v-model="activeIndex" :before-leave="beforeTabLevae" @tab-click="tabClicked">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="" prop="goods_cat">
              <el-cascader v-model="addForm.goods_cat" :options="cateList" :props="{ expandTrigger: 'hover', ...cateProps }" @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- item -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox border :label="item" v-for="(item, i) in item.attr_vals" :key="i">{{ item }}</el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!--
             action API
             handlePreview 
             handleRemove 
             fileList 
             list-type 
            -->
            <el-upload
            class="upload-demo"
            :action="uploadURL"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            list-type="picture"
            :headers="headersObj"
            :on-success="handleSuccess">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  Two-way Data-Binding -->
            <quill-editor ref="myQuillEditor" v-model="addForm.goods_introduce" />
            <!--            -->
            <el-button type="primary" class="AddBtn" @click="addShop"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--    -->
    <el-dialog title="" :visible.sync="previewDialogVisible" width="50%">
      <img :src="previewPath" alt="" class="perviewImg" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
import { goodsAddFormRulesMixin } from './mixin.js'
import Breadcrumb from './Breadcrumb'
export default {
  name: 'Add',
  components: {
    Breadcrumb
  },
  mixins: [goodsAddFormRulesMixin],
  data() {
    return {
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        attrs: []
      },
      // 
      cateList: [],
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // URL
      uploadURL: 'https://lianghj.top:8888/api/private/v1/upload',
      // headers
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewPath: '',
      // 
      previewDialogVisible: false
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.cateList = res.data
      console.log(this.cateList)
    },
    // 
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
      console.log(this.addForm.goods_cat)
    },
    //  Tabs 
    beforeTabLevae(activeName, oldActiveName) {
      // console.log(': ', oldActiveName)
      // console.log(': ', activeName)
      if ((oldActiveName === '0' && this.addForm.goods_cat.length !== 3) || this.addForm.goods_name === '') {
        this.$message.error('!  ')
        return false
      }
    },
    async tabClicked() {
      // 
      if (this.activeIndex === '1') {
        console.log('')
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: { sel: 'many' }
        })
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }
        res.data.forEach(item => (item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')))
        // console.log(res)
        this.manyTableData = res.data
      }
      if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: { sel: 'only' }
        })
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }
        this.onlyTableData = res.data
        console.log(this.onlyTableData)
      }
    },
    // 
    handlePreview(file) {
      this.previewPath = file.response.data.url.replace('http://127.0.0.1', 'https://lianghj.top')
      this.previewDialogVisible = true
      console.log('', file.response.data.url)
    },
    // 
    handleRemove(file) {
      // 1. 
      const filePath = file.response.data.tmp_path
      // 2.  pics 
      const index = this.addForm.pics.findIndex(x => x.pic === filePath)
      // 3. splice,,pics
      this.addForm.pics.splice(index, 1)
      console.log('', file, this.addForm)
    },
    // 
    handleSuccess(resposne) {
      // 1. 
      const picInfo = { pic: resposne.data.tmp_path }
      // 2. ,pushpics
      this.addForm.pics.push(picInfo)
      console.log(resposne)
      console.log(this.addForm)
    },
    addShop() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        // 
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        // 
        this.manyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_vals: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        // 
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_vals: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        // console.log(form)
        // 
        // 
        const { data: res } = await this.$http.post('goods', form)
        console.log(res)
        if (res.meta.status !== 201) {
          return this.$message.error('!')
        }
        this.$message.success('!')
        this.$router.push('/goods')
        //    goods_cat key
        // this.addForm.goods_cat = this.addForm.goods_cat.join(',')
      })
    }
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-steps {
  margin: 15px 0;
}
.el-step__title {
  font-size: 13px;
}
.el-checkbox {
  margin: 0 5px 0 0 !important;
}
.perviewImg {
  width: 100%;
}
.AddBtn {
  margin-top: 15px;
}
</style>
