<template>
  <div class="list">
    <Breadcrumb name1="" name2="" />
    <el-card>
      <!-- / -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" @clear="getGoofsList" clearable v-model="queryInfo.query">
            <el-button slot="append" icon="el-icon-search" @click="getGoofsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>
      <!-- table -->
      <el-row>
        <el-table :data="goodsList" border style="width: 100%">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="goods_name" label=""></el-table-column>
          <el-table-column prop="goods_price" label="()" width="110px"></el-table-column>
          <!-- <el-table-column
            prop="goods_number"
            width="120px"
            label=""
          ></el-table-column> -->
          <el-table-column prop="goods_weight" label="" width="130px"></el-table-column>
          <el-table-column width="220px" label="">
            <template v-slot="scope">
              {{ scope.row.add_time | dataFormat }}
            </template>
          </el-table-column>
          <el-table-column label="" width="150px">
            <template v-slot="scope">
              <el-button type="primary" size="mini" icon="el-icon-edit"></el-button>
              <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeById(scope.row.goods_id)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[7, 11, 15, 19]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import Breadcrumb from './Breadcrumb.vue'
export default {
  name: 'List',
  components: {
    Breadcrumb
  },
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 7
      },
      // 
      goodsList: [],
      // 
      total: 0
    }
  },
  created() {
    this.getGoofsList()
  },
  methods: {
    // 
    async getGoofsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.goodsList = res.data.goods
      this.total = res.data.total
      console.log(res.data)
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoofsList()
    },
    // 
    handleCurrentChange(newValue) {
      this.queryInfo.pagenum = newValue
      this.getGoofsList()
    },
    async removeById(id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('!')
      }
      const { data: res } = await this.$http.delete('goods/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.getGoofsList()
    },
    goAddpage() {
      this.$router.push('goods/add')
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  margin: 15px 0 0;
}
</style>
