import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login' //login
import Home from '../components/Home' //login
import Welcome from '../components/Welcome' //login
import Users from '../components/user/Users' //login
import Rights from '../components/power/Rights' //login
import Roles from '../components/power/Roles' //login
import Cate from '../components/goods/Cate' //login
import Params from '../components/goods/Params' //login
import GoodsList from '../components/goods/List' //login
import Add from '../components/goods/Add' //login


import Order from '../components/order/Order' //login
import Report from '../components/report/Report' //login

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/', redirect: '/login' }, //'/''login'
    { path: '/login', component: Login },
    {
      path: '/home',
      component: Home,
      //
      redirect: '/welcome',
      children: [
        { path: '/welcome', component: Welcome },
        { path: '/users', component: Users },
        { path: '/rights', component: Rights },
        { path: '/roles', component: Roles },
        { path: '/categories', component: Cate },
        { path: '/params', component: Params },
        { path: '/orders', component: Order },
        { path: '/reports', component: Report },
        {path: '/goods',component: GoodsList},
        {path: '/goods/add',component: Add},
        
      ],
    },
  ],
})

//
router.beforeEach((to, from, next) => {
  //to
  //from
  //next 
  //next()   next('/login')

  if (to.path === '/login') return next()
  //token
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')
  next()
})
export default router //
