<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <el-row>
                <el-col :span="8">
                    <el-input placeholder="">

                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>

            <!--  -->
            <el-table :data="orderlist" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="order_number"></el-table-column>
                <el-table-column label="" prop="order_price"></el-table-column>
                <el-table-column label="" prop="pay_status">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
                        <el-tag type="danger" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="is_send"></el-table-column>
                <el-table-column label="" prop="create_time">
                    <template slot-scope="scope">
                        {{ (scope.row.create_time) | formonment('YYYY-MM-DD HH:mm:ss') }}
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
                        <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox">
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>

            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15]" :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="addressVisible" width="50%" @close="addressDialogClosed">
            <el-form ref="addressFormRef" :model="addressForm" label-width="100px" :rules="addressFormRules">
                <el-form-item label="/" prop="address1">
                    <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="addressForm.address2"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressVisible = false"> </el-button>
                <el-button type="primary" @click="addressVisible = false"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="progressVisible" width="50%">
            <!--  -->
            <el-timeline>
                <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.ftime" type="success">
                    {{ activity.context }}
                </el-timeline-item>
            </el-timeline>

        </el-dialog>
    </div>
</template>
<script>
import cityData from "./citydata.js";

export default {

    data() {
        return {
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 10,
            },
            // 
            total: 0,
            orderlist: [],
            // 
            addressVisible: false,
            progressVisible: false,

            addressForm: {
                address1: [],
                address2: ''
            },
            addressFormRules: {
                address1: [
                    { required: true, message: '/', trigger: 'blur' }
                ],
                address2: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            cityData,
            progressInfo: []


        }
    },
    created() {
        this.getOrderList()
    },
    methods: {
        async getOrderList() {
            const { data: res } = await this.$http.get('orders', { params: this.queryInfo })

            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            console.log(res);
            this.total = res.data.total
            this.orderlist = res.data.goods
        },
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getOrderList()
        },
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getOrderList()
        },
        // 
        showBox() {
            this.addressVisible = true
        },
        // 
        addressDialogClosed() {
            this.$refs.addressFormRef.resetFields()
        },
        async showProgressBox() {
            const { data: res } = await this.$http.get('/kuaidi/1106975712662')
            console.log(res);
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.progressInfo = res.data
            this.progressVisible = true
        }
    },
}
</script>
<style lang="less" scoped>
.el-cascader {
    width: 100%;
}
</style>
